<template>
  <div id="userManage">
    <div class="btn_box">
      <Button size="small" type="primary" @click.stop="$refs.add.build = true">新建</Button>
    </div>
    <div class="tab_box">
      <Table ref="table" :columns="columns1" :data="data1"  size="small" :height="tableHeight" ></Table>
    </div>
    <div class="page_box">
      <Page :total="total" :page-size="pageSize" show-elevator size="small" @on-change="pageChange" show-total/>
    </div>
   <VersionDbAdd ref="add"  @getNewData="getTableData"></VersionDbAdd>
    <!-- <UserEdit ref="userEdit" :data="editDefaultData" :codeData="codeData" :departmentList="departmentList" @getTableData="getTableData"></UserEdit>-->
  </div>
</template>

<script>
import { dbVersionList, dbVersionDelete } from '@/api/systemManage'
import { renderTime } from '@/utils/common'
import VersionDbAdd from './VersionDb_add'
import UserEdit from './User_edit'
export default {
  name: 'VersionDb',
  data () {
    return {
      /* 表格动态高度 */
      tableHeight: Math.floor((window.innerHeight - 310 - 32) / 40) * 40 + 32,
      /* 每页条数 */
      pageSize: Math.floor((Math.floor((window.innerHeight - 310 - 32) / 40) * 40 + 32) / 40),
      /* 页码 */
      pageNum: 1,
      /* 页面总条数 */
      total: 0,
      /* 编辑框默认原数据 */
      editDefaultData: {},
      /* 表格 */
      columns1: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '版本',
          key: 'version'
        },
        {
          title: '创建人',
          key: 'createUser'
        },
        {
          title: '创建日期',
          key: 'createDate',
          render: (h, params) => {
            return h('span', renderTime(params.row.createDate))
          }
        },
        {
          title: '备注',
          key: 'remark'
        },
        {
          title: '操作',
          key: 'action',
          width: 250,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              /* h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editDefaultData = params.row
                    this.$refs.userEdit.edit = true
                  }
                }
              }, '编辑'), */
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该版本么？',
                      onOk: () => {
                        this.sysDel(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: []
    }
  },
  components: {
    VersionDbAdd,
    UserEdit
  },
  mounted () {
    /* 监听浏览器高度 */
    window.onresize = function () {
      this.tableHeight = Math.floor((window.innerHeight - 288 - 32) / 40) * 40 + 32
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 288 - 32) / 40) * 40 + 32) / 40)
    }.bind(this)
    this.getTableData()
  },
  methods: {
    /* axios: 获取列表数据 */
    getTableData () {
      dbVersionList({
        data: {
          pageSize: this.pageSize,
          pageNum: this.pageNum
        }
      }).then((res) => {
        console.log(res)
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 删除数据 */
    sysDel (id) {
      dbVersionDelete({
        data: {
          id: id
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (p) {
      this.pageNum = p
      this.getTableData()
    }
  }
}
</script>

<style scoped lang="scss">
#userManage{
  .btn_box{
    padding:10px 15px 0;
    text-align: right;
  }
  .tab_box{
    padding: 10px;
  }
  .page_box{
    text-align: right;
    padding:0 10px
  }
}
</style>
