<template>
  <div id="user_add">
    <Modal
      v-model="build"
      title="新建"
      class-name="vertical-center-modal"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="version" label="版本">
          <Input v-model.trim="AddData.version" placeholder="请输入版本" size="small"/>
        </FormItem>
        <FormItem label="db文件"  prop="file">
          <Upload
            v-model="AddData.file"
            :before-upload="handleUpload"
            :accept="'.db,.zip'"
            :format="['.db','.zip']"
            :max-size=102400
            action="#"
            ref="upload"
          >
            <Button size="small">选择文件</Button>
          </Upload>
          <span style="margin-left: 10px">
              文件名称:
              <span v-if="AddData.file === null">未选择文件</span>
              <span v-if="AddData.file !== null">{{ AddData.file.name }}</span>
            </span>
        </FormItem>
        <FormItem prop="addRealName" label="备注信息">
          <Input v-model.trim="AddData.remark" placeholder="请输入版备注信息" size="small"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="verification" :loading="loadingStatus">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { dbVersionAdd } from '@/api/systemManage'
export default {
  name: 'VersiondbAdd',
  data () {
    // 自定义验证 判断上传文件 fileList 的长度, 这样就和普通输入框表现一致了
    const validateUpload = (rule, value, callback) => {
      if (this.AddData.file === null) {
        callback(new Error('请选择要上传的文件'))
      } else {
        callback()
      }
    }
    return {
      /* 添加数据 */
      AddData: {
        version: '',
        remark: '',
        file: null
      },
      AddRule: {
        version: [
          { required: true, message: '版本号不能为空', trigger: 'blur' },
          { pattern: /^([1-9]\d*|0)(\.\d{1,2})?$/, message: '请输入正确格式的版本号，例：1.0', trigger: 'blur' }
        ],
        file: [
          { required: true, validator: validateUpload, trigger: 'change' }
        ]
      },
      /* 新建框 */
      build: false,
      loadingStatus: false
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    /* 上传excal坐标文件 */
    handleUpload (file) {
      this.AddData.file = file
      this.$refs['add'].validate(() => {})
    },
    /* 提交 */
    upload () {
      let param = new FormData()
      param.append('file', this.AddData.file)
      param.append('version', this.AddData.version)
      param.append('remark', this.AddData.remark)
      param.append('createBy', this.$store.getters.getloginInfor().userId)
      this.params = param
      dbVersionAdd(this.params).then((res) => {
        console.log(res)
        if (res.data.code === '0000') {
          this.loadingStatus = false
          this.$Notice.success({ title: '上传成功' })
          this.$emit('getNewData')
          this.build = false
          this.init()
        } else {
          this.loadingStatus = false
          this.$Notice.error({ title: res.data.msg })
        }
      })
    },
    /* 验证 */
    verification () {
      this.loadingStatus = true
      this.$refs['add'].validate((valid) => {
        if (valid) {
          this.upload()
        } else {
          this.loadingStatus = false
        }
      })
    },
    /* 初始化 */
    init () {
      this.AddData = {
        version: '',
        remark: '',
        file: null
      }
      this.$refs.upload.clearFiles()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
<style lang="scss">
  .vertical-center-modal{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: left;
    .ivu-modal{
      top: 0;
    }
  }
</style>
